.form-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-weight: bold;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

label {
  font-weight: bold;
}

input,
select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
  width: 100%;
}

/* ========== NEW SHARED STYLES ========== */

.action-button {
  padding: 12px 20px;
  background-color: #0066ff;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.action-button:hover {
  background-color: #004bb5;
}

/* Align top * button to the right */
.top-button-wrapper {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

/* Center register button */
.bottom-button-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}
